import { StatsCard } from "@/components/dashboard/stats-card";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { formatCurrency, formatDateTime } from "@/lib/utils/emi-calculator";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import {
  Users,
  Wallet,
  ArrowLeftRight,
  Landmark,
  ArrowDownLeft,
  ArrowUpRight,
  Clock,
  CheckCircle2,
  ArrowRight,
} from "lucide-react";
import dbConnect from "@/lib/mongodb";
import { Profile, Account, Transaction, Loan } from "@/lib/models";

export default async function AdminDashboardPage() {
  await dbConnect();

  const totalUsers = await Profile.countDocuments();
  const totalAccounts = await Account.countDocuments();

  const transactions = await Transaction.find()
    .sort({ createdAt: -1 })
    .limit(100)
    .lean();
  const loans = await Loan.find().lean();

  const pendingKYC = await Profile.find({ kycStatus: "pending" })
    .limit(5)
    .lean();

  const pendingLoans = await Loan.find({ status: "pending" })
    .populate("userId", "fullName email")
    .sort({ createdAt: -1 })
    .limit(5)
    .lean();

  const recentTransactions = await Transaction.find()
    .populate("userId", "fullName email")
    .populate("accountId", "accountNumber")
    .sort({ createdAt: -1 })
    .limit(5)
    .lean();

  const totalDeposits =
    transactions
      ?.filter((t) => t.type === "deposit" || t.type === "transfer_in")
      .reduce((sum, t) => sum + Number(t.amount), 0) || 0;

  const totalWithdrawals =
    transactions
      ?.filter((t) => t.type === "withdrawal" || t.type === "transfer_out")
      .reduce((sum, t) => sum + Math.abs(Number(t.amount)), 0) || 0;

  const pendingLoansCount =
    loans?.filter((l) => l.status === "pending").length || 0;
  const approvedLoansCount =
    loans?.filter((l) => ["approved", "disbursed", "active"].includes(l.status))
      .length || 0;
  const totalLoanAmount =
    loans
      ?.filter((l) => ["disbursed", "active"].includes(l.status))
      .reduce((sum, l) => sum + Number(l.amount), 0) || 0;

  return (
    <div className="space-y-8">
      {/* Page Header */}
      <div className="space-y-2">
        <h1 className="text-3xl font-bold tracking-tight text-foreground">
          Admin Dashboard
        </h1>
        <p className="text-muted-foreground">Overview of your banking system</p>
      </div>

      {/* Main Stats Grid - 4 columns */}
      <div className="grid gap-6 md:grid-cols-2 xl:grid-cols-4">
        <StatsCard
          title="Total Users"
          value={totalUsers.toString()}
          description="Registered users"
          icon={Users}
          iconClassName="bg-primary/10 text-primary"
        />
        <StatsCard
          title="Total Accounts"
          value={totalAccounts.toString()}
          description="Active bank accounts"
          icon={Wallet}
          iconClassName="bg-accent/10 text-accent"
        />
        <StatsCard
          title="Total Deposits"
          value={formatCurrency(totalDeposits)}
          description="This month"
          icon={ArrowDownLeft}
          iconClassName="bg-emerald-500/10 text-emerald-500"
        />
        <StatsCard
          title="Total Withdrawals"
          value={formatCurrency(totalWithdrawals)}
          description="This month"
          icon={ArrowUpRight}
          iconClassName="bg-destructive/10 text-destructive"
        />
      </div>

      {/* Secondary Stats Grid - 4 columns */}
      <div className="grid gap-6 md:grid-cols-2 xl:grid-cols-4">
        <StatsCard
          title="Pending Loans"
          value={pendingLoansCount.toString()}
          description="Awaiting approval"
          icon={Clock}
          iconClassName="bg-amber-500/10 text-amber-500"
        />
        <StatsCard
          title="Active Loans"
          value={approvedLoansCount.toString()}
          description="Currently active"
          icon={Landmark}
          iconClassName="bg-primary/10 text-primary"
        />
        <StatsCard
          title="Loan Portfolio"
          value={formatCurrency(totalLoanAmount)}
          description="Total disbursed"
          icon={Landmark}
          iconClassName="bg-accent/10 text-accent"
        />
        <StatsCard
          title="Transactions"
          value={transactions.length.toString()}
          description="Recent transactions"
          icon={ArrowLeftRight}
          iconClassName="bg-muted-foreground/10 text-muted-foreground"
        />
      </div>

      {/* Additional Sections Grid */}
      <div className="grid gap-6 lg:grid-cols-2">
        {/* Pending Loans Card */}
        <Card className="border-0 shadow-sm">
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-4">
            <CardTitle className="text-lg font-semibold">
              Pending Loans
            </CardTitle>
            <Link href="/admin/loans">
              <Button variant="ghost" size="sm" className="gap-2">
                View All
                <ArrowRight className="h-4 w-4" />
              </Button>
            </Link>
          </CardHeader>
          <CardContent className="space-y-4">
            {pendingLoans.length === 0 ? (
              <p className="py-8 text-center text-sm text-muted-foreground">
                No pending loans
              </p>
            ) : (
              pendingLoans.map((loan: any) => (
                <div
                  key={loan._id.toString()}
                  className="flex items-center justify-between rounded-lg border border-border/50 p-4 transition-colors hover:bg-muted/50"
                >
                  <div className="space-y-1">
                    <p className="text-sm font-medium">
                      {loan.userId?.fullName || "Unknown User"}
                    </p>
                    <p className="text-xs text-muted-foreground">
                      {loan.userId?.email}
                    </p>
                  </div>
                  <div className="text-right">
                    <p className="text-sm font-semibold">
                      {formatCurrency(loan.amount)}
                    </p>
                    <Badge variant="secondary" className="mt-1">
                      {loan.loanType}
                    </Badge>
                  </div>
                </div>
              ))
            )}
          </CardContent>
        </Card>

        {/* Recent Transactions Card */}
        <Card className="border-0 shadow-sm">
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-4">
            <CardTitle className="text-lg font-semibold">
              Recent Transactions
            </CardTitle>
            <Link href="/admin/transactions">
              <Button variant="ghost" size="sm" className="gap-2">
                View All
                <ArrowRight className="h-4 w-4" />
              </Button>
            </Link>
          </CardHeader>
          <CardContent className="space-y-4">
            {recentTransactions.length === 0 ? (
              <p className="py-8 text-center text-sm text-muted-foreground">
                No recent transactions
              </p>
            ) : (
              recentTransactions.map((txn: any) => (
                <div
                  key={txn._id.toString()}
                  className="flex items-center justify-between rounded-lg border border-border/50 p-4 transition-colors hover:bg-muted/50"
                >
                  <div className="flex items-center gap-3">
                    <div
                      className={`flex h-10 w-10 items-center justify-center rounded-full ${
                        txn.type === "deposit" || txn.type === "transfer_in"
                          ? "bg-emerald-500/10 text-emerald-500"
                          : "bg-destructive/10 text-destructive"
                      }`}
                    >
                      {txn.type === "deposit" || txn.type === "transfer_in" ? (
                        <ArrowDownLeft className="h-5 w-5" />
                      ) : (
                        <ArrowUpRight className="h-5 w-5" />
                      )}
                    </div>
                    <div className="space-y-1">
                      <p className="text-sm font-medium capitalize">
                        {txn.type.replace("_", " ")}
                      </p>
                      <p className="text-xs text-muted-foreground">
                        {txn.userId?.fullName || "Unknown"}
                      </p>
                    </div>
                  </div>
                  <div className="text-right">
                    <p
                      className={`text-sm font-semibold ${
                        txn.type === "deposit" || txn.type === "transfer_in"
                          ? "text-emerald-500"
                          : "text-destructive"
                      }`}
                    >
                      {txn.type === "deposit" || txn.type === "transfer_in"
                        ? "+"
                        : "-"}
                      {formatCurrency(Math.abs(Number(txn.amount)))}
                    </p>
                    <p className="text-xs text-muted-foreground">
                      {formatDateTime(new Date(txn.createdAt))}
                    </p>
                  </div>
                </div>
              ))
            )}
          </CardContent>
        </Card>
      </div>

      {/* Pending KYC Section */}
      {pendingKYC.length > 0 && (
        <Card className="border-0 shadow-sm">
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-4">
            <CardTitle className="text-lg font-semibold">
              Pending KYC Approvals
            </CardTitle>
            <Link href="/admin/users">
              <Button variant="ghost" size="sm" className="gap-2">
                View All
                <ArrowRight className="h-4 w-4" />
              </Button>
            </Link>
          </CardHeader>
          <CardContent>
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
              {pendingKYC.map((profile: any) => (
                <div
                  key={profile._id.toString()}
                  className="flex items-center justify-between rounded-lg border border-border/50 p-4 transition-colors hover:bg-muted/50"
                >
                  <div className="space-y-1">
                    <p className="text-sm font-medium">{profile.fullName}</p>
                    <p className="text-xs text-muted-foreground">
                      {profile.email}
                    </p>
                  </div>
                  <Badge variant="secondary">Pending</Badge>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
